.apexcharts-tooltip.apexcharts-theme-light {
       border: none !important;
       background: var(--bs-body-bg) !important;
       -webkit-box-shadow: 0 0 50px 0 rgba(82, 63, 105, .15) !important;
       box-shadow: 0 0 50px 0 rgba(82, 63, 105, .15) !important;
}

.apexcharts-tooltip.apexcharts-theme-light .apexcharts-tooltip-title {
       background: rgba(var(--bs-dark-rgb), 0.05) !important;
       border-bottom: none !important;
}

.apexcharts-xaxistooltip {
       background: var(--bs-body-bg) !important;
       border-radius: 0.42rem !important;
       -webkit-box-shadow: 0 0 15px 0 rgba(var(--bs-dark-rgb), .15) !important;
       box-shadow: 0 0 15px 0 rgba(var(--bs-dark-rgb), .15) !important;
       border: 0 !important;
}

.apexcharts-xaxistooltip-bottom:after,
.apexcharts-xaxistooltip-bottom:before {
       border-bottom-color: var(--bs-body-bg) !important;
}

.apexcharts-tooltip-y-group,
.apexcharts-xaxistooltip-text {
       color: var(--bs-gray-800);
}

.apexcharts-legend-text {
       margin-left: -12px;
       color: var(--bs-gray-800) !important;
}

.apexcharts-text tspan {
       fill: var(--bs-gray-800) !important;
}

.apexchartsanalyticsOverview .apexcharts-graphical line,
#screenRange .apexcharts-graphical line {
       stroke: var(--bs-gray-300) !important;
}

.topTagsWrapper,
.assetsBySource,
.top_creators {
       height: 360px;
}

.user_profile_items {
       max-width: 300px;
       width: 100%;
}

// .mapChart svg path:hover{
//     fill: var(---bs-primary-bg);
// }
.amcharts-map-area {
       fill: var(--bs-gray-300);
       stroke: var(--bs-gray-100);
}

.amcharts-map-area.active {
       fill: var(--bs-primary);
}

// .geoTooltip{
//     position: absolute;
//     width: 100%;
//     height: 100%;
// }

g[aria-labelledby*="-title"] {
       display: none;
}

body .form-select-multi.form-select-multi-sm button span {
       max-width: 200px;
}

body .apexcharts-theme-light .apexcharts-menu{
       padding: 0.5rem;
       font-size: calc(8px + 0.1vw + 0.1vh) !important;
       border: none;
       min-width: 120px;
       font-weight: 500;
       border-radius: 8px;
       box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.15);
}

body .apexcharts-theme-light .apexcharts-menu-item{
       background-color: transparent;
       color: var(--bs-dropdown-link-color);
       border-radius: 4px;
       text-align: left;
}
body .apexcharts-theme-light .apexcharts-menu-item:hover{
       color: var(--bs-primary);
       background-color: var(--bs-gray-100);
}

.donut-chart .apexcharts-tooltip-y-group,
.donut-chart .apexcharts-xaxistooltip-text {
       color: var(--bs-white);
}

.donut-chart .apexcharts-datalabel-value,
.donut-chart .apexcharts-datalabel-label{
       fill: var(--bs-gray-800);
}

.bar_chart .apexcharts-datalabel{
       fill: var(--bs-gray-800);
}
@media (min-width: 991px) {
       .topperform_content {
              max-height: 435px;
              overflow-y: auto;
       }

       .user_profile_items {
              max-width: 120px;
       }
}

@media (min-width: 1280px) {
       .top-performing-modal .modal-dialog {
              max-width: 1000px;
       }
       .web_tabs .col-xl-3{
              width: 20%;
       }

}

